@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&family=Spartan:wght@500;700&display=swap);
html{
  font-size: 15px;
  box-sizing: border-box; 
  font-family: 'Spartan', sans-serif;
}


body{
    background-color: hsl(180, 52%, 96%);
    margin: auto;
    min-width: 375px;
    }

.App{
  position: relative;
}

ul {
  list-style: none;
  padding-left: 0;
}

span:empty{
  display: none;
}

button:empty{
  display: none;
}

.btn{
  margin: .5rem;
  background-color: hsl(180, 31%, 95%);
  padding: .25rem;
  color: hsl(180, 29%, 50%);
  cursor: pointer;
}


nav{
  height: 8rem;
  background-image: url(/Jobs-Listing-with-filter-React-App/static/media/bg-header-mobile.9cb13612.svg);
  background-color: hsl(180, 29%, 50%);
}

.tags-panel{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  background-color: white;
  padding: 0 1.5rem;
  border-radius: .5rem;
  box-shadow: .25rem .5rem 1rem hsl(180, 17%, 70%);
  position: relative;
  top: -20px;
  margin: 0 2rem;
  
}

.tags-list{
  display: flex;
}

.tags{
  display: flex;
  align-items: center;
  background-color: hsl(180, 31%, 95%);
  border-radius: .25rem;
  margin-right: 1rem; 

}

.tag-span{
  color:  hsl(180, 40%, 43%);
  padding: 0 .25rem;

}

.close{
  border: none;
  padding: 0;
  height: 21.5px;
  background: none;
}

.close:hover{
  cursor: pointer;
}

.cross{
  background-color: hsl(180, 40%, 43%);
  padding: .25rem;
  border-top-right-radius: .25rem;
  border-bottom-right-radius: .25rem; 
}

.clear-span{
  color: hsl(180, 8%, 52%) ;
}

.clear-span:hover{
  color:hsl(180, 40%, 43%);
  cursor: pointer;
  text-decoration: underline;
}

h1 {
  text-align: left;
  font-size: 1rem;
  color: hsl(180, 14%, 20%);
}

h1:hover{
  color: hsl(180, 40%, 43%)
}

.btn-skills{
  margin-left: 0;
  border: none;
  border-radius: 5px;
  color: hsl(182, 71%, 42%)
}

.btn-skills:hover{
  background-color: hsl(180, 29%, 50%);
  color: white;
}

.company-details{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-color: white;
  border-radius: .25rem;;
  padding: 4rem 1rem 1rem;
  margin: 4rem 2rem 2rem;
  box-shadow: .25rem .5rem 1rem hsl(180, 17%, 70%);
  }

.logo{
  position: relative;
}

.job-description{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 0;
  border-bottom: 1px solid hsl(180, 9%, 79%);
  margin-bottom: 1rem;
}

.details{
  margin-bottom: 1rem;
  text-align: left;
}

.details-span{
  color: gray;
  font-size: .9rem;
}
.img{
  position:absolute;
  top: -90px;
  left: 0;
  width: 60px;
  height: auto;
}

.company-span{
    margin-right: .5rem;
  }

.company{
    color: hsl(180, 29%, 50%);
    font-weight: 700;
  }

.company-details:nth-of-type(1), .company-details:nth-of-type(2){
    border-left: 3px solid hsl(180, 40%, 43%)
  }

.post{
    background-color: hsl(180, 29%, 50%);
    color: white;
    padding: .25rem .5rem;
    border-radius: 1rem;
    font-weight: 700;
    font-size: .75rem;
  }
  
  
.featured{
    background-color: hsl(180, 14%, 20%);
    color: white;
    padding: .25rem .5rem;
    border-radius: 1rem;
    font-weight: 700;
    font-size: .75rem;
  }

.skills{
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  max-width: 80%;
}



.tags{
  margin-top: .5rem;
}

.tag-span{
  font-size: .75rem;
}

.tags-list{
  flex-direction: row;
  flex-wrap: wrap;
  
}

.attribution{
  font-size: .75rem;
  margin-top: auto;
}

.kiran{
  color: hsl(244, 80%, 47%);
}

@media(min-width: 900px){
  body {
  max-width: 1440px;
}

.img{
  position: static;
  width: auto;
}
nav{
  height: 8rem;
  background-image: url(/Jobs-Listing-with-filter-React-App/static/media/bg-header-desktop.8694f466.svg);
  background-color: hsl(180, 29%, 50%);
}

.tags-panel{
  margin: 0 5rem
}

.company-details{
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  border-radius: .25rem;;
  padding: 1rem;
  margin: 2rem 5rem;
  box-shadow: .25rem .5rem 1rem hsl(180, 17%, 70%);
  }

.logo{
  display: flex;
  position: static;
}

.job-description{
  margin-left: 1rem;
  border-bottom: none;
  margin-bottom: 0;
  justify-content: space-around;
  
}

.company-span{
    margin-right: .5rem;
  }

.company{
  color: hsl(180, 29%, 50%);
}

.details{
  margin-bottom: 0;
}

.details-span{
    margin-right: .5rem;
  }



}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

